.App {
  text-align: center;
}
#quote-box{
  padding: 30px;
  background-color: white;
  border-radius: 15px;
  color:black;
}
#author{
  text-align: end;
 }
#tweet-quote{
  float:left;
  color:white;
  border-radius: 10px;
  padding:10px;
 text-decoration: none;
}
#new-quote{
  float:right;
  color:white;
  border-radius: 10px;
  padding:10px;
  border: none;
  cursor: pointer;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

